import React from 'react';
import DrumMachine from './DrumMachine';
import '../../App.css';

class DrumApp extends React.Component {
  constructor(){
    super();
    this.state = {
      keyInput: 'Click or press keys to play.'
    }
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.playSound = this.playSound.bind(this);
  }

  componentDidMount(){
    document.addEventListener('keypress', (event)=>{
      this.handleKeyPress(event);
    });
  }
  componentWillUnmount(){
    document.removeEventListener('keypress', (event)=>{
      this.handleKeyPress(event);
    });
  }

  handleClick(event){
    const val = event.target.innerText;
    this.playSound(val);
    this.setState({
      keyInput: val
    })
  }
  handleKeyPress(event){
    const val = event.key;
    this.playSound(val);
    this.setState({
      keyInput: val
    })
  }
  playSound(key){
    let sound;
    switch(key){
      case 'Q':
      case 'q': 
        sound = document.getElementById('Q');
        break;
      case 'W':
      case 'w': 
        sound = document.getElementById('W');
        break;
      case 'E':
      case 'e': 
        sound = document.getElementById('E');
        break;  
      case 'A':
      case 'a': 
        sound = document.getElementById('A');
        break; 
      case 'S':
      case 's': 
        sound = document.getElementById('S');
        break; 
      case 'D':
      case 'd': 
        sound = document.getElementById('D');
        break; 
      case 'Z':
      case 'z': 
        sound = document.getElementById('Z');
        break; 
      case 'X':
      case 'x': 
        sound = document.getElementById('X');
        break; 
      case 'C':
      case 'c': 
        sound = document.getElementById('C');
        break;                   
      default: return;
    }
    sound.currentTime = 0;
    sound.play();
    
  }
  render(){
    return (
      <div className="App">
        <DrumMachine text={this.state.keyInput} 
        clickController={this.handleClick}
        />
      </div>
    );
  }
}

export default DrumApp;
