import React from 'react';
import TextBox from './TextBox';
import '../../App.css';

const placeholder = 
`# some h1 size text
## some h2 size text
**bold text**

> This is a block quote.

Magic inline html tags \`<p></p> \`
\`\`\` 
const myFunction = ()=> {Do All the Things}
\`\`\`

This is a [link to FreeCodeCamp](https://www.freecodecamp.com)


- Unordered List 
  - Indented
    - Hi
1. ordering
2. of 
3. the things

![React Logo w/ Text](https://goo.gl/Umyytc)

`;


class TextApp extends React.Component {
  constructor(){
    super();
    this.state = {
      text: ""
    }
    this.handleChange = this.handleChange.bind(this);
  }
  componentDidMount(){
    this.setState({
      text: placeholder
    })
  }
  handleChange(event){
    const val = event.target.value;
    this.setState({
      text: val
    })
  }
  render(){
    return (
      <div className="App">
        <TextBox onChange={this.handleChange} text={this.state.text}/>
      </div>
    );
  }
}

export default TextApp;
