import React from 'react';
import '../../App.css';
import Calculator from './Calculator';
import {evaluate} from 'mathjs';

class CalcApp extends React.Component {
  constructor(){
    super();
    this.state = {
      inputDisplay: '0',    
      currentNumber: '0',   
      formulaEntered: ''    
    }
    this.handleInput = this.handleInput.bind(this);
  }

  handleInput(event){  
    const val = event.target.innerText;   //switch cases.  only specific vals added to string for later evaluation

    //mutually exclusive setState calls.  
    switch(val){  
      //reset all values with clear
      case 'clear':
        this.setState({
          inputDisplay: '0',
          currentNumber: '0',
          formulaEntered: ''
        })
        break;
        
      //add decimal if currentNumber does not already contain a decimal
      case '.':
        if(this.state.currentNumber.indexOf('.') < 0){

          this.setState({
            currentNumber: this.state.currentNumber + val,
            inputDisplay: this.state.inputDisplay + val
          })
          
        }
        break;

      //add 0 only if input string is not 0.   
      case '0':
        if (this.state.inputDisplay !== '0') {
          this.setState({
            currentNumber: this.state.currentNumber + val,
            inputDisplay: this.state.inputDisplay + val
          })
        }
        break;

      case '1':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        //do not allow a leading 0.  todo: limit number length
        if (this.state.inputDisplay !== '0'){
          this.setState({
            currentNumber: this.state.currentNumber + val,
            inputDisplay: this.state.inputDisplay + val
          })
        } 
        else {
          this.setState({
            currentNumber: val,
            inputDisplay: val
          })
        }
        break;

      case '+':
      case '-':
      case '*':
      case '/':
        //currentNumber is empty only if prior key was a number (initial state is 0).
        if(this.state.currentNumber !== ''){
          this.setState({
            currentNumber: '',  //clear currentNumber entry
            inputDisplay: this.state.inputDisplay + val
          })
        }
        //replace last operator.
        else{
            let inputCopy = [...this.state.inputDisplay];
            inputCopy.pop();    
            inputCopy.join(''); 
            this.setState({ 
              inputDisplay: inputCopy + val
            })
          }
        break;

      case '=':
        //set input to evaluated value and reset currentNumber to 0.
        let formulaEntered = this.state.inputDisplay;
        let result = evaluate(this.state.inputDisplay).toString();
        this.setState({
          currentNumber: '0',
          formulaEntered,
          inputDisplay: result
        })
        break;   
        default: return;  
    }
    
  }

  render(){
    return (
      <div className="App">
          <Calculator input={this.state.formulaEntered} output={this.state.inputDisplay} clickController={this.handleInput} />
      </div>
    );
  }
}

export default CalcApp;
